<script>
let map;
export default {
  name: "Map",
  data() {
    return {}
  },
  mounted() {

    // 百度地图API功能
    map = new BMap.Map('container'); // 创建Map实例
    map.centerAndZoom(new BMap.Point(112.923784, 28.225152), 12); // 初始化地图,设置中心点坐标和地图级别
    map.enableScrollWheelZoom(true); // 开启鼠标滚轮缩放
    var myGeo = new BMap.Geocoder();
// 将地址解析结果显示在地图上，并调整地图视野
    myGeo.getPoint("长沙市湖南工商大学岳麓道569号", function (point) {
          if (point) {
            map.centerAndZoom(point, 16);
            map.addOverlay(new BMap.Marker(point));
          }
        },
        "长沙市");

